import MenuOutlined from '@/icons/MenuOutlined.vue'
//import AccountBalanceWalletSharp from '@/icons/AccountBalanceWalletSharp.vue';

const mainMenuOptions = [
  {
    label: () => h(RouterLink, {
      to: {
        name: "home"
      }
    }, { default: () => "Home" }),
    key: "home"
  },
  {
    label: () => h(RouterLink, {
      to: {
        name: "mint"
      }
    }, { default: () => "Mint" }),
    key: "mint"
  },
  {
    label: () => h(RouterLink, {
      to: {
        name: "claim"
      }
    }, { default: () => "Claim" }),
    key: "claim"
  },
  {
    label: () => h(RouterLink, {
      to: {
        name: "upgrade"
      }
    }, { default: () => "Upgrade" }),
    key: "upgrade"
