        name: "mint"
      }
    }, { default: () => "Mint" }),
    key: "mint"
  },
  {
    label: () => h(RouterLink, {
      to: {
        name: "claim"
      }
    }, { default: () => "Claim" }),
    key: "claim"
  },
  {
    label: () => h(RouterLink, {
      to: {
        name: "upgrade"
      }
    }, { default: () => "Upgrade" }),
    key: "upgrade"
  },
  {
    label: () => h(RouterLink, {
      to: {
        name: "open"
      }
    }, { default: () => "Open" }),
