    const activeKeyRef = ref('');
    const mobilePopoverRef = ref(null)
    const router = useRouter()
    // common
    const isMobileRef = useIsMobile();
    const isTabletRef = useIsTablet();
    const isSmallDesktopRef = useIsSmallDesktop();

    window.$message = useMessage();

    function handleLogoClick () {
      router.push('/');
      activeKeyRef.value = 'home';
    }

    function handleUpdateMobileMenu () {
      mobilePopoverRef.value.setShow(false)
    }

    return {
      mobilePopoverRef,
      isMobile: isMobileRef,
      isTablet: isTabletRef,
      isSmallDesktop: isSmallDesktopRef,
      activeKey: activeKeyRef,
      mainMenuOptions,
      mobileMenuOptions,
      handleLogoClick,
      handleUpdateMobileMenu,
      storeConnect
    }
