var contractAddress = '0xBE82b9533Ddf0ACaDdcAa6aF38830ff4B919482C';


const Web3 = window.Web3;
import Web3Modal from "web3modal";
import WalletConnectProvider from '@walletconnect/web3-provider/dist/umd/index.min.js';

function pricePerOneOutput(price) {
  return price==10?'?':parseFloat((price/1000000000000000000).toFixed(3));
}

function priceTotalOutput(price, amount) {
  return price==0?'0':price==10?'?':parseFloat((amount*price/1000000000000000000).toFixed(3));
}

    let provider;
    let selectedAccount;
    let contract;
    let contractInit = true;


    const providerOptions = {
      walletconnect: {
        package: WalletConnectProvider, // required
        options: {
          infuraId: "ae1a633eeb1843c0b020cfa641758e1a" // required
        }
      }
    };

    const web3Modal = new Web3Modal({
      network: "mainnet", // optional
      cacheProvider: false, // optional
      providerOptions, // required
      disableInjectedProvider: false, // optional. For MetaMask / Brave / Opera.
    });

    const priceRef = ref(10);
    const mintAmountRef = ref(1);
    const totalSupplyRef = ref(0);

    const astrConnectButtonRef = ref('CONNECT');
    //const tooltipConnectButtonRef = ref("Disabled");
    const tooltipConnectButtonRef = ref("Connect Wallet");

    initLoad();

    async function initLoad() {
      //var web3 = new Web3('wss://mainnet.infura.io/ws/v3/ae1a633eeb1843c0b020cfa641758e1a');
      var web3 = new Web3('wss://mainnet.infura.io/ws/v3/ae1a633eeb1843c0b020cfa641758e1a');
      contract = new web3.eth.Contract(abi, contractAddress);
    
      var supply = await contract.methods.totalSupply().call();
      totalSupplyRef.value = parseInt(supply);
      priceRef.value = await contract.methods.price().call();
    }

    async function onConnect() {
      console.log("Opening a dialog", web3Modal);
      try {
        provider = await web3Modal.connect();
      } catch(e) {
        console.log("Could not get a wallet connection", e);
        window.$message.error("Could not get a wallet connection");
        return;
      }

      // Subscribe to accounts change
      provider.on("accountsChanged", (accounts) => {
        console.log("accounts: ", accounts);
        fetchAccountData();
      });

      // Subscribe to chainId change
      provider.on("chainChanged", (chainId) => {
        console.log("chainId: ", chainId);
        fetchAccountData();
      });

      // Subscribe to networkId change
      provider.on("networkChanged", (networkId) => {
        console.log("networkId: ", networkId);
        fetchAccountData();
      });

      await refreshAccountData();
    }

    async function onDisconnect() {

      console.log("Killing the wallet connection", provider);

      // TODO: Which providers have close method?
      if(provider.close) {
        await provider.close();

        // If the cached provider is not cleared,
        // WalletConnect will default to the existing session
        // and does not allow to re-scan the QR code with a new wallet.
        // Depending on your use case you may want or want not his behavir.
        await web3Modal.clearCachedProvider();
        provider = null;
      }

      selectedAccount = null;
      updateWalletDOM(0x0);
    }

    async function fetchAccountData() {
      // Get a Web3 instance for the wallet
      const web3 = new Web3(provider);
      console.log("Web3 instance is", web3);

      // Get connected chain id from Ethereum node
      const chainId = await web3.eth.getChainId();
      console.log("chainId: ", chainId);
      // Load chain information over an HTTP API
      //const chainData = evmChains.getChain(chainId);
      //document.querySelector("#network-name").textContent = chainData.name;

      // Get list of accounts of the connected wallet
      const accounts = await web3.eth.getAccounts();     

      // MetaMask does not give you all accounts, only the selected account
      console.log("Got accounts", accounts);
      selectedAccount = accounts[0];

      updateWalletDOM(selectedAccount);
      web3.eth.defaultAccount = selectedAccount;
      contract = new web3.eth.Contract(abi, contractAddress);
      contractInit = false; 
    }

    async function refreshAccountData() {
      // Disable button while UI is loading.
      // fetchAccountData() will take a while as it communicates
      // with Ethereum node via JSON-RPC and loads chain data
      // over an API call.
      disabledBtnConnectRef.value = true;
      await fetchAccountData(provider);
      disabledBtnConnectRef.value = false;
    }

    function updateWalletDOM(address) {
      if(!selectedAccount) {
          astrConnectButtonRef.value = "CONNECT";
          tooltipConnectButtonRef.value = "Connect Wallet";
          //console.log(astrConnectButtonRef);
          return;
      }
      astrConnectButtonRef.value = address.toString().substr(0,5) + "..." + address.toString().substr(address.length-3,3);
      //console.log(astrConnectButtonRef);
      tooltipConnectButtonRef.value = "Click to disconnect";
    }

    function onWalletBtn () {
      if(!selectedAccount){
          onConnect();
      } else {
          onDisconnect();
      }
    }

    function handleUpdateAmount(amount) {
      //totalPriceRef.value = (amount*priceRef.value).toFixed(3);
      mintAmountRef.value = amount;
    }
    
    async function mintNft() {

      if(!selectedAccount) {
        await onConnect();
      }
      if(contract && !contractInit) {
        
        //var activeSale = await contract.methods.mintIsActive().call();
        //if(!activeSale) {
        //    window.$message.warning("Sale has not started.");
        //    return;
        //}
     
        var supply = await contract.methods.totalSupply().call().catch((e)=>{
                alert(e);
            });
        
        priceRef.value = await contract.methods.price().call();
        //supply = 10000;
        //totalSupplyRef.value = ('0000' + supply).slice(-4)+"/10000";
        totalSupplyRef.value = supply;
        //console.log(parseInt(supply)+mintAmountRef.value);
        if(parseInt(supply) >= 10000) {         
            window.$message.warning("Sale has ended.");
            return;
        } else if(parseInt(supply) + mintAmountRef.value > 10000) {
            window.$message.error(`Purchase amount: ${mintAmountRef.value} exceeds supply.`);
            return;
        }            
        /*var isPublic = await contract.methods.mintIsActive().call();
        
        if(!isPublic) {
            window.$message.info("Public sale has not started yet.");
            return;
        }*/
        
        //output.json
        //let signature;
        //let sigraw = await fetch('output.json');
        //let sigjson = await sigraw.json();
        //let sigraw = await fetch('./output.json');
        //let sigjson = await sigraw.json();

        //console.log(sigjson[selectedAccount]);'
        
        let errorsend = false;
        
       // console.log(estimatedGas);
        try {
          let estimatedGas = await contract.methods
        .mint(mintAmountRef.value)
        .estimateGas({ from: selectedAccount, value: (priceRef.value*parseInt(mintAmountRef.value)) });
        estimatedGas = Math.floor((estimatedGas*1.10)+15000);
        await contract.methods.mint(mintAmountRef.value).send({from: selectedAccount, value: (priceRef.value*parseInt(mintAmountRef.value)), gasLimit:estimatedGas});//180000*parseInt(mintAmountRef.value)
        } catch (error) {
          errorsend = true;
          window.$message.warning(error["message"]);
        }
        

        if(!errorsend)
        window.$message.warning("Check your Metamask history for confirmation.");
        
        /*if(sigjson["0x604667fc8f9df40e2808ABE167a20362B1E79B23"] !== 'undefined') {
            await contract.methods.allowlistMint(mintAmountRef.value,sigjson[0x604667fc8f9df40e2808ABE167a20362B1E79B23]).send({from: selectedAccount, value: (60000000000000000*parseInt(mintAmountRef.value))});
            //console.log("fd");
            window.$message.error("Check your Metamask history for confirmation.");
        } else {
            window.$message.error("You are not whitelisted.");
        }*/
        //.then(response => response.json())
        //.then(data => console.log(data));
        
      }
    }

export const storeConnect = reactive({
    astrConnectButton: astrConnectButtonRef,
    tooltipConnectButton: tooltipConnectButtonRef,
    selectedAccount: null,
    
    onWalletBtn,
    handleUpdateAmount,
    mintNft,
    mintAmount: mintAmountRef,
    price: priceRef,
    totalSupply: totalSupplyRef,
    disabledBtnConnect: disabledBtnConnectRef,
    pricePerOne: computed(() => {
    return pricePerOneOutput(priceRef.value)
    }),
    priceTotal: computed(() => {
    return priceTotalOutput(priceRef.value, mintAmountRef.value)
    }),
    totalDeadlineMintClaim,
});

