import { nextTick } from 'vue';
import { createRouter, createWebHashHistory } from 'vue-router';
import HomeView from '../views/HomeView1.vue';
//import { publicPath } from '../../vue.config';

export const loadingBarApiRef = {}

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/home/faq',
    name: 'faq',
    component: () => import('../views/HomeFAQView.vue')
  },
  {
    path: '/home/faq2',
    name: 'faq2',
    component: () => import('../views/HomeFAQ2View.vue')
  },
  {
    path: '/home/check',
    name: 'check',
    component: () => import('../views/HomeCheckView1.vue')
  },
  {
    path: '/mint',
    name: 'mint',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/MintView1.vue')
  },
  {
    path: '/claim',
    name: 'claim',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/ClaimView1.vue')
  },
  {
    path: '/upgrade',
    name: 'upgrade',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import('../views/UpgradeView.vue')
  },
  {
    path: '/open',
    name: 'open',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/OpenView.vue')
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  //base: publicPath,
  routes
})


export default function createSiteRouter () {

  router.beforeEach(function (to, from, next) {
    if (!from || to.path !== from.path) {
      if (loadingBarApiRef.value) {
        loadingBarApiRef.value.start()
      }
    }
    next()
  })

  router.afterEach(function (to, from) {
    if (!from || to.path !== from.path) {
      if (loadingBarApiRef.value) {
        loadingBarApiRef.value.finish()
      }
      if (to.hash && to.hash !== from.hash) {
        nextTick(() => {
          const el = document.querySelector(to.hash)
          if (el) el.scrollIntoView()
        })
      }
    }
  })

  return router
}
